import { Component } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent {

  timezone: string = null;
  disabled = false;
  placeholderString = 'Choose a timezone';
  onChange(t: string) {
    console.log(t);
  }

}



